 @import './mixins.scss';

 %container {
 	width: 80%;
 	margin: 5px auto;
 }

 %btnLink {
 	position: relative;
 	@include flex (row, center, center);
 	font-family: $secondaryFont;
 	color: $textColor;
 	width: 200px;
 	padding: 10px 0;
 	text-align: center;
 	text-decoration: none;
 	font-size: 19px;
 	letter-spacing: 1px;
 	z-index: 1;

 	&:before {
 		content: '';
 		position: absolute;
 		background-color: $decorColor;
 		width: 40%;
 		height: 10px;
 		bottom: 0;
 		left: 60px;
 		transition: all 0.2s ease-in-out 0s;
 	}

 	&:hover {
 		&:before {
 			width: 100%;
 			height: 100%;
 			z-index: -1;
 			left: 0;
 			bottom: 0;
 			transition: all 0.3s ease-in-out 0s;
 		}
 	}
 }